@import '../../assets/css/variable.less';
*{
  box-sizing: border-box;
}
.airdrop-box{
  border-top: 1px solid @divider_color;
  color: #FFFFFF;
  font-size: 18px;
  font-weight: normal;
  .airdrop-info{
    width: 100%;
    height: 786px;
    margin-top: 28px;
    background-image: url('./img/1568bg.jpg');
    background-repeat: no-repeat;
    padding-top: 119px;
    background-position: center;
    .airdrop-info-box{
      width: 737px;
      height: 436px;
      margin: 0 auto;
      text-align: center;
      a{
        text-decoration: underline;
      }
      .airdrop-title{
        font-size: 56px;
        font-weight: 500;
      }
      .wallet-address{
        margin-top: 45px;
        .blue-logo{
          display: inline-block;
          width: 7px;
          height: 10px;
          border-radius: 50%;
          background-color: @second_color;
          margin-right: 6px;
        }
      }
      .wallet-balance{
        margin-top: 15px;
      }
      .airdrop-time{
        width: 347px;
        height: 81px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin: 0 auto;
        padding-left: 10px;
        margin-top: 56px;
        .time-logo{
          width: 55px;
          height: 55px;
          background-color: #2564F4;
          border-radius: 5px;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-right: 8px;
        }
        .time-text{
          text-align: left;
          display: flex;
          justify-content: space-between;
          .time-title{
            margin-right: 10px;
          }
        }
      }
      .position{
        margin-top: 20px;
      }
      .position-text{
        justify-content: flex-start;
        .time-title{
          margin-left: 4px;
        }
      }
      .claim{
        display: flex;
        justify-content: center;
        margin-top: 44px;
        div{
          color: #FFFFFF;
        }
      }
    }
  }
}
@media only screen and (min-width : 760px) and (max-width:1140px) {
  .airdrop-box{
   
    .airdrop-info{
      width: 100%;
      height: 585px;
      margin-top: 3px;
      background-image: url('./img/1140bg.jpg');
      padding-top: 81px;
    }
  }
}

@media only screen and (min-width : 375px) and (max-width:760px) {
  .airdrop-box{
    .airdrop-info{
      width: 100%;
      height: 585px;
      margin-top: 3px;
      background-image: url('./img/760bg.jpg');
      padding-top: 81px;
      .airdrop-info-box{
        width: 100%;
      }
    }
  }
}
@media only screen and  (max-width:375px) {
  .airdrop-box{
    .airdrop-info{
      width: 100%;
      height: 100vh;
      margin-top: 3px;
      background-image: url('./img/375bg.jpg');
      padding-top: 81px;
      .airdrop-info-box{
        width: 100%;
        .airdrop-title{
          text-align: start;
          padding-left: 28px;
          width: 259px;
        }
      }
    }
  }
}

